import React, { Component } from 'react';
import Table from 'react-bootstrap/Table';
import './Card.css';

class Card extends Component {
    constructor(props){
        super(props);

        this.onClick = this.onClick.bind(this);

        this.state = {
            reversed: false
        };
    }

    onClick(){
        const newReversed = !this.state.reversed;

        this.setState({
            ...this.state,
            reversed: newReversed
        });
    }

    render(props){
        return(
            <div className="card-container">
                <div 
                    className={"card " + (this.state.reversed ? "reversed" : "")}
                    onClick={this.onClick}
                    >
                    <div className="front">
                        <div className="id">{this.props.card.id}</div>
                        <div className="hebrew">
                            <span className="be">{this.props.card.hebrew}</span>
                            <img style={{display: !!this.props.card.hebrewPrefix ? 'inline' : 'none' }}
                                src={'nikud/' + this.props.card.hebrewPrefix}/>
                        </div>
                        <div className="front-annotation">{this.props.card.frontAnnotation}</div>
                    </div>
                    <div className="back">
                        <div className="id">{this.props.card.id}</div>
                        <Table bordered hover>
                            <tbody>
                                <tr>
                                    <td className="translit">Transliteração</td>
                                    <td className= "inner emphasis">{this.props.card.translit}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.name ? 'table-row' : 'none' }}>
                                    <td className="name">Nome</td>
                                    <td className="inner emphasis">{this.props.card.name}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.hebName ? 'table-row' : 'none' }}>
                                    <td className="hebName">Nome Heb.</td>
                                    <td className="inner">{this.props.card.hebName}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.sound ? 'table-row' : 'none' }}>
                                    <td className="sound">Som</td>
                                    <td className="inner">{this.props.card.sound}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.meaning ? 'table-row' : 'none' }}>
                                    <td className="meaning">Significado</td>
                                    <td className="inner">{this.props.card.meaning}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.type ? 'table-row' : 'none' }}>
                                    <td className="type">Tipo</td>
                                    <td className="inner">{this.props.card.type}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.value ? 'table-row' : 'none' }}>
                                    <td className="value">Valor</td>
                                    <td className="inner">{this.props.card.value}</td>
                                </tr>
                                <tr style={{display: !!this.props.card.verseLink ? 'table-row' : 'none' }}>
                                    <td className="value">Versículo</td>
                                    <td className="inner"><a href={this.props.card.verseLink} target="_blank" rel="noopener noreferrer">{this.props.card.verseRef || 'Versículo'}</a></td>
                                </tr>
                            </tbody>
                        </Table>
                    </div>
                </div>
            </div>
        )
    }
}

export default Card
