import 'bootstrap/dist/css/bootstrap.min.css';

import React, { Component } from 'react';
import './App.css';
import Card from './Card/Card';
import DrawButton from './DrawButton/DrawButton';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import CARDS from './Data/cards.json';

class App extends Component {
  constructor(props){
    super(props);

    this.updateCard = this.updateCard.bind(this);
    this.getRandomCard = this.getRandomCard.bind(this);
    this.changeCardType = this.changeCardType.bind(this);

    this.state = {
      cards: CARDS.alfabeto,
      currentCardType: 'alfabeto',
      currentCard: null,
      currentIndex: -1
    };
  }

  changeCardType(newCardType) {
    if (newCardType === this.state.currentCardType) {
      return;
    }

    const cardsList = CARDS[newCardType];
    const newCard = this.getRandomCard(undefined, cardsList);

    this.setState({
      cards: cardsList,
      currentCardType: newCardType,
      currentCard: newCard.card,
      currentIndex: newCard.index
    });
  }

  componentWillMount(){
    this.updateCard();
  }

  getRandomCard(currentCard, cardsList){
    var randomIndex = Math.floor(Math.random() * cardsList.length);
    var card = cardsList[randomIndex];
    
    if(currentCard && card.id === currentCard.id){
      return this.getRandomCard(currentCard, cardsList)
    }

    return {
      card,
      index: randomIndex
    };
  }

  updateCard(increment){
    const cardsList = this.state.cards;
    let newCard;

    if (!increment) {
      newCard = this.getRandomCard(this.state.currentCard, cardsList);
    } else {
      let newIndex = (this.state.currentIndex + increment) % cardsList.length;

      if (newIndex < 0) {
        newIndex = cardsList.length + newIndex;
      }
      
      newCard = {
        card: cardsList[newIndex],
        index: newIndex
      };
    }

    this.setState({
      ...this.state,
      currentCard: newCard.card,
      currentIndex: newCard.index
    });
  }

  render() {
    return (
      <div className="App">
        <Container>
          <Row>
            <Col>
              <Navbar className="justify-content-center">
                <Nav
                    
                    activeKey={this.state.currentCardType}
                    onSelect={(selectedKey) => this.changeCardType(selectedKey)}
                  >
                  <Nav.Item>
                    <Nav.Link eventKey="alfabeto">Alfabeto</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="vogais">Vogais</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="vocabulario">Vocabulário</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="sinais">Sinais</Nav.Link>
                  </Nav.Item>
                </Nav>
              </Navbar>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="cardRow">
                <Card card={this.state.currentCard}/>
              </div>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="buttonRow">
                <DrawButton drawCard={this.updateCard}/>
              </div>
            </Col>
          </Row>
        </Container>
        
      </div>
    );
  }
}

export default App;
