import React, { Component } from 'react';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button';
import './DrawButton.css';

class DrawButton extends Component {
    constructor(props){
        super(props);

        this.drawCard = this.drawCard.bind(this);
        this.randomCard = this.randomCard.bind(this);
        this.nextCard = this.nextCard.bind(this);
        this.previousCard = this.previousCard.bind(this);
    }

    drawCard(increment){
        this.props.drawCard(increment);
    }


    randomCard() {
        this.drawCard();
    }

    previousCard() {
        this.drawCard(-1);
    }

    nextCard() {
        this.drawCard(1);
    }

    render(props){
        return(
            <div className="buttonContainer">
                <ButtonGroup aria-label="Basic example" size="lg">
                    <Button onClick={this.previousCard} >&nbsp;&lt;&nbsp;</Button>
                    <Button variant="secondary" onClick={this.randomCard}>Aleatório</Button>
                    <Button onClick={this.nextCard}>&nbsp;&gt;&nbsp;</Button>
                </ButtonGroup>
            </div>
        )
    }
}

export default DrawButton